import React from 'react';

import CheckService from '../../services/check-service';

import WorkerCardBody from '../worker-card-body';
import shortid from 'shortid';
import { Animated } from "react-animated-css";

class WorkerInfoPanel extends React.Component{

    checkService = new CheckService();

    state = {
        contentType: null,
        success: false,
        danger: false,
        key: shortid.generate(),
    }

    componentDidUpdate(prevProps, prevState){
        if (prevProps.count !== this.props.count){
            this.getContentType();
        };
    }

    getContentType = () => {
        if (!this.props.url){ return; };
        this.checkService.getManifestContentType(this.props.url)
            .then((type) => {
                if (type.includes('application/json')){
                    this.setState({
                        success: true,
                        danger: false,
                        contentType: type,
                        key: shortid.generate(),
                    });
                }else{
                    this.setState({
                        danger: true,
                        success: false,
                        contentType: type,
                        key: shortid.generate(),
                    })
                }
            })
            .catch(this.onErrorContentType);
    }

    onErrorContentType = (err) => {
        this.setState({
            danger: true,
            success: false,
            contentType: 'not found',
            key: shortid.generate(),
        });
    }

    setPanelType = (state) =>{
        let classNames = 'card mb-3';
        if (!state.success && 
            !state.warning &&
            !state.danger){
                classNames += ' border-primary'
            }else if (state.success){
                classNames += ' border-success'
            }else if (state.danger){
                classNames += ' border-danger'
            }
        return classNames;
    }

    render(){
        const maxWidthStyle = {
            'maxWidth': '20rem',
        };

        const { contentType } = this.state;
        const classNames = this.setPanelType(this.state);

        const { worker } = this.props;

        return(
            <Animated animationIn="bounceIn" animationOut="bounceOut" isVisible={true} 
            animateOnMount={true} key={this.state.key}>
                <div className={classNames} 
                    style={maxWidthStyle}>
                    <div className="card-header"><h4>{worker}</h4></div>
                        <div className="card-body">
                            <WorkerCardBody type={contentType}/>
                        </div>
                </div>
            </Animated>
        )
    }
}

export default WorkerInfoPanel;