import React from 'react';
import ReactDOM from 'react-dom';


import ManifestInfoPanel from './components/manifest-info-panel';
import WorkerInfoPanel from './components/worker-info-panel';
import IndexPageInfoPanel from './components/index-page-info-panel';

import './index.css';

class SearchInput extends React.Component{

    getUrl = () => {
        let url = this.urlValue.value.trim();
        if (url && !url.match(/^http([s]?):\/\/.*/)){
            url = 'https://' + url.replace(/\/$/, '');
        }
        this.props.onClick(url);
    }

    render() {
        return (
            <div className="form-inline input-width">
                <input 
                    //className="form-control my-2"
                    className="form-control mr-sm-2"
                    type = "text"
                    placeholder = "https://example.com"
                    ref={ el => this.urlValue = el }
                    />
                <button
                    onClick={this.getUrl}
                    className="btn btn-secondary my-2 my-sm-0"
                    >Проверить</button>
            </div>
        );
    };
}

class App extends React.Component{

    state = {
        url: 'https://sirvitalas.github.io',
        count: 0,
    }

    updateUrl = (newUrl) => {
        this.setState({
            url: newUrl,  
            count: this.state.count + 1,    
        });
    }

    render(){ 
        return (
            <div >
                <h1 className="center-me">Проверка настроек push сервиса</h1>
                <SearchInput onClick={this.updateUrl}/>
                <p/>
                <div className="one-line">
                        <ManifestInfoPanel url={this.state.url} count={this.state.count}/>
                        <WorkerInfoPanel worker='sp-push-worker-fb.js' url={this.state.url} count={this.state.count}/>
                        <IndexPageInfoPanel url={this.state.url} count={this.state.count}/>
                </div>
            </div>
        );
    }
}

ReactDOM.render(<App />, document.getElementById("root"));
