import React from 'react';

const WorkerCardBody = ({ type }) => {

    let typeStatus;

    if (type && type.includes('application/json')){
        typeStatus = <font color='lime'>{type}</font>;
    } else{
        typeStatus = <font color='red'>{type}</font>;
    }

    return(
        <div className="card-body">
                    <p className="card-text"><b>Expected content type:</b>  application/json</p>
                    <p className="card-text"><b>Current content type:</b> {typeStatus}</p>
        </div>
    );
}

export default WorkerCardBody;