import React from 'react';

import CheckService from '../../services/check-service'

import ManifestCardBody from '../manifest-card-body'
import { Animated } from "react-animated-css";
import shortid from 'shortid';

class ManifestInfoPanel extends React.Component{

    checkService = new CheckService();

    state = {
        contentType: null,
        senderId: null,
        success: false,
        warning: false,
        danger: false,
        key: shortid.generate(),
    }

    componentDidUpdate(prevProps, prevState){
        if (prevProps.count !== this.props.count){
            this.getContentType();
            this.getGcmSenderId();
        };
    }

    onErrorContentType = (err) => {
        this.setState({
            danger: true,
            success: false,
            contentType: 'not found',
            key: shortid.generate(),
        });
    }

    onErrorSendrId = (err) => {
        this.setState({
            danger: true,
            success: false,
            senderId: null,
        });
    }

    getContentType = () => {
        if (!this.props.url){ return; };
        this.checkService.getManifestContentType(this.props.url)
            .then((type) => {
                if (type.includes('application/json')){
                    this.setState({
                        success: true,
                        danger: false,
                        contentType: type,
                        key: shortid.generate(),
                    });
                }else{
                    this.setState({
                        danger: true,
                        success: false,
                        contentType: type,
                        key: shortid.generate(),
                    })
                }
            })
            .catch(this.onErrorContentType);
    }

    getGcmSenderId = () => {
        if (!this.props.url){ return; };
        this.checkService.getGcmSenderId(this.props.url)
            .then((body) => {
                if (Object.values(body).includes('300013155679')){
                    this.setState({
                        success: true,
                        danger: false,
                        senderId: true,
                    });
                } else{
                    this.setState({
                        danger: true,
                        success: false,
                        senderId: false,
                    })
                }
            })
            .catch(this.onErrorSendrId);
    }

    setPanelType = (state) =>{
        let classNames = 'card mb-3';
        if (!state.success && 
            !state.warning &&
            !state.danger){
                classNames += ' border-primary'
            }else if (state.success){
                classNames += ' border-success'
            }else if (state.danger){
                classNames += ' border-danger'
            }
        return classNames;
    }

    render(){
        const maxWidthStyle = {
            'maxWidth': '20rem',
        };

        const { contentType, senderId } = this.state;
        const classNames = this.setPanelType(this.state);
        return(
 
                <Animated animationIn="bounceIn" animationOut="bounceOut" isVisible={true} 
                        animateOnMount={true} key={this.state.key}>
                    <div className={classNames} 
                        style={maxWidthStyle}>
                        <div className="card-header"><h4>sp-push-manifest.json</h4></div>
                            <ManifestCardBody type={contentType} senderId={senderId}/>
                    </div>
                </Animated>
        )
    }
}

export default ManifestInfoPanel;