import React from 'react';

import CheckService from '../../services/check-service'

import IndexPageCardBody from '../index-page-card-body';

import { Animated } from "react-animated-css";
import shortid from 'shortid';

class IndexPageInfoPanel extends React.Component{
    checkService = new CheckService();

    state = {
        manifest: null,
        success: false,
        warning: false,
        danger: false,
        key: shortid.generate(),
    }

    componentDidUpdate(prevProps, prevState){
        if (prevProps.count !== this.props.count){
            this.isManifestPresent();
        };
    }

    isManifestPresent = () => {
        if (!this.props.url){ return; };
        this.checkService.getIndexPageBody(this.props.url)
            .then((res) => {
                if (res.includes('manifest')){
                    this.setState({
                        success: false,
                        manifest: true,
                        warning: true,
                        danger: false,
                        key: shortid.generate(),
                    })
                }else {
                    this.setState({
                        success: true,
                        warning: false,
                        danger: false,
                        manifest: false,
                        key: shortid.generate(),
                    })
                }
            })
            .catch((err) => {
                this.setState({
                    warning: false,
                    danger: true,
                    success: false,
                    manifest: null,
                    key: shortid.generate(),
                });
            })
    }

    setPanelType = (state) =>{
        let classNames = 'card mb-3';
        if (!state.success && 
            !state.warning &&
            !state.danger){
                classNames += ' border-primary'
            }else if (state.success){
                classNames += ' border-success'
            }else if (state.warning){
                classNames += ' border-warning'
            }else if (state.danger){
                classNames += ' border-danger'
            }
        return classNames;
    }

    render(){
        const maxWidthStyle = {
            'maxWidth': '20rem',
        };

        const { manifest } = this.state;
        const classNames = this.setPanelType(this.state);

        return(
            <Animated animationIn="bounceIn" animationOut="bounceOut" isVisible={true} 
            animateOnMount={true} key={this.state.key}>
                <div className={classNames} 
                    style={maxWidthStyle}>
                    <div className="card-header"><h4>Index page</h4></div>
                        <IndexPageCardBody manifest={manifest} />
                </div>
            </Animated>
        )
    }
}

export default IndexPageInfoPanel;
