import React from 'react';

const ManifestCardBody = ({ type, senderId }) => {

    let senderIdStatus, typeStatus;

    if (senderId == null){
        senderIdStatus = <font color='red'>not found</font>;
    } else if (senderId){
        senderIdStatus = <font color='lime'>correct</font>;
    } else{
        senderIdStatus = <font color='red'>incorrect</font>;
    }

    if (type && type.includes('application/json')){
        typeStatus = <font color='lime'>{type}</font>;
    } else{
        typeStatus = <font color='red'>{type}</font>;
    }

    return(
        <div className="card-body">
                    <p className="card-text"><b>Expected content type:</b>  application/json</p>
                    <p className="card-text"><b>Current content type:</b> {typeStatus}</p>
                    <p className="card-text"><b>gcm_sender_id:</b> {senderIdStatus} </p>
        </div>
    );
}

export default ManifestCardBody;