import React from 'react';

const IndexPageCardBody = ({ manifest }) => {
    
    let manifestStatus;
    if (manifest == null){
        manifestStatus = <font color='red'>index page not found</font>;
    }else if (!manifest){
        manifestStatus = <font color='lime'>manifest not found</font>;
    } else {
        manifestStatus = <font color='red'>manifest was found!</font>;
    }

    return(
        <div className="card-body">
                    <p className="card-text"><b>Expected result:</b>  not found</p>
                    <p className="card-text"><b>Checking for manifest:</b> {manifestStatus}</p>
        </div>
    );
}

export default IndexPageCardBody;