export default class CheckService {
   
    _oldWorker = "/sp-push-worker.js";
    _newWorker = "/sp-push-worker-fb.js";
    _manifest  = "/sp-push-manifest.json";

    async _checkType(url){
        const res = await fetch(url); //{mode: 'no-cors'});
        if(res.ok){
            return await res.headers.get('Content-Type');
        }
        return 'not found';
    }

    async getGcmSenderId(url){
        const res = await fetch(`${url}${this._manifest}`);
        if(res.ok){
            return await res.json();
        }
        return 'not found';
    }

    async getIndexPageBody(url){
        const res = await fetch(`${url}`);
        if(res.ok){
            return await res.text();
        }
        return res.status;
    }

    getManifestContentType(url){
        return this._checkType(`${url}${this._manifest}`);
    }

    getNewWorkerContentType(url){
        return this._checkType(`${url}${this._newWorker}`);
   }

   getOldWorkerContentType(url){
        return this._checkType(`${url}${this._oldWorker}`);
    }
}